<script>
// @ is an alias to /src
export default {
  name: "page",
  data(){
    return {
      scrollAnim: this.anim(),
      navStatus: false
    }
  },
  components: {
    
  },
  mounted(){
    let fadeInUp_items = this.$refs.page.querySelectorAll('.fade-in-up');
    this.$parent.fadeInUp(fadeInUp_items);

    for (let i = fadeInUp_items.length - 1; i >= 0; i--) {
      this.$parent.fadeInUpCalc(fadeInUp_items[i])
    }
  },
  methods: {
    easeInOutCubic(t) {
      return t<.5 ? 4*t*t*t : (t-1)*(2*t-2)*(2*t-2)+1;
    },
    anim() {
      let temp;
      let vue = this;

      return function(sel) {
        cancelAnimationFrame(temp);

        let start = performance.now(),
            from = window.pageYOffset || document.documentElement.scrollTop,
            to = vue.$refs.page.querySelector(sel).getBoundingClientRect().top;
        requestAnimationFrame(function step(timestamp) {
          let progress = vue.easeInOutCubic((timestamp - start) / 1500);

          1 <= progress && (progress = 1);

          window.scrollTo(0, from + to * progress | 0);

          1 > progress && (temp = requestAnimationFrame(step))
        })
      }
    }
  }
};
</script>

