<template>
  <div class="app" style="overflow: hidden;">
    <transition name="fade">
      <router-view />
    </transition>
  </div>
</template>

<script>
// @ is an alias to /src
export default {
  name: "app",
  data(){
    return {
      company: "Company",
      product: "Minimino"
    }
  },
  methods: {
    fadeInUpCalc(item){
      if(window.pageYOffset + window.innerHeight - 100 > item.offsetTop + item.offsetParent.offsetTop){
        item.style.transform = "translateY(0)";
        item.style.opacity = 1;
      }else{
        item.style.transform = "translateY(150px)";
        item.style.opacity = 0;
      }
    },
    fadeInUp(items){
      for (let i = items.length - 1; i >= 0; i--) {
        window.addEventListener("scroll", ()=>{
          this.fadeInUpCalc(items[i])
        }, true)
      }
    }
  }
};
</script>


<style lang="scss">
  @import '@/assets/scss/main.scss';
</style>
